import styled from "@emotion/styled"
import { Link } from "gatsby"
import {
  backgroundColor,
  backgroundDarkColor,
  themeColor,
} from "../../styles/colors"
import React from "react"

export const StyledSideBar = styled.div`
  display: grid;
  grid-template-rows: max-content 1fr;
  height: 100vh;
  width: max-content;
  padding: 20px 4px;
  align-items: center;
  justify-items: center;
  background-color: ${backgroundDarkColor};
  border-right: 1px solid #05060a;
`

export const StyledNav = styled.nav`
  display: flex;
  flex-direction: column;
  align-items: center;
`

export const StyledFooter = styled.footer`
  display: grid;
  grid-auto-rows: max-content;
  grid-row-gap: 16px;
`
export const StyledFooterItem = styled.a`
  display: grid;
  font-size: 14px;
  color: white;
  text-decoration: none;
  cursor: pointer;
`
export const StyledNavItem = styled(({ active, ...props }) => (
  <Link {...props} />
))`
  display: grid;
  color: ${({ active }) => (active ? themeColor : "white")};
  text-decoration: none;
  justify-items: center;
  font-size: 20px;
  padding: 20px 4px;
  grid-row-gap: 8px;
  div {
    display: flex;
    font-size: 12px;
    align-content: center;
    align-items: center;
    transition: all 0.3s;
    span {
      transition: all 0.3s;
      color: ${({ active }) => (active ? themeColor : "transparent")};
    }
  }
`
