import React, { useEffect, useState } from "react"
import {
  StyledFooter,
  StyledFooterItem,
  StyledNav,
  StyledNavItem,
  StyledSideBar,
} from "./styles"
import { Logo } from "../Logo"
import { useLocation } from "@reach/router"
import Icon from "@iconify/react"
import bxlStackOverflow from "@iconify-icons/bx/bxl-stack-overflow"
import githubFill from "@iconify-icons/akar-icons/github-fill"
import linkedinIcon from "@iconify-icons/bi/linkedin"
import baselineEmail from "@iconify-icons/ic/baseline-email"
import handPeace from "@iconify-icons/la/hand-peace"
import helpInfoLine from "@iconify-icons/clarity/help-info-line"
import codeIcon from '@iconify-icons/carbon/code';
import cloudDownload from '@iconify-icons/bi/cloud-download';
import keyboardIcon from '@iconify-icons/bi/keyboard';

export const SideBar = ({ layout }) => {
  const [offset, setOffset] = useState(0)
  useEffect(() => {
    setOffset(layout.current?.scrollTop ?? 0)
    layout.current.onscroll = function (e) {
      setOffset(layout.current?.scrollTop ?? 0)
    }
  }, [layout])
  return (
    <StyledSideBar show={offset > 0}>
      <Logo />
      <StyledNav>
        <NavItem icon={handPeace} title={"Hello"} to={"/"} />
        <NavItem icon={helpInfoLine} title={"About"} to={"/about"} />
        <NavItem icon={codeIcon} title={"Services"} to={"/services"} />
        <NavItem icon={cloudDownload} title={"Shop"} to={"/shop"} />
        <NavItem icon={keyboardIcon} title={"Blog"} to={"/blog"} />
      </StyledNav>
      <StyledFooter>
        <FooterItem to={"https://github.com/besufkadmenji"} icon={githubFill} />
        <FooterItem
          to={"https://stackoverflow.com/users/10239185/besufkad-menji"}
          icon={bxlStackOverflow}
        />
        <FooterItem
          to={"https://www.linkedin.com/in/besufkadmenji"}
          icon={linkedinIcon}
        />
        <FooterItem
          to={"mailto:besufkadmenji@gmail.com"}
          icon={baselineEmail}
        />
      </StyledFooter>
    </StyledSideBar>
  )
}

const FooterItem = ({ icon, to }) => {
  return (
    <StyledFooterItem target={"_blank"} href={to}>
      <Icon icon={icon} />
    </StyledFooterItem>
  )
}

export const NavItem = ({ title, to, icon }) => {
  const location = useLocation()
  useEffect(() => {
    console.log(location.pathname)
  }, [])
  return (
    <StyledNavItem to={`${to}`} active={to === location.pathname}>
      <Icon icon={icon} />
      <div>
        <span>{"<"}</span>
        {title}
        <span>{">"}</span>
      </div>
    </StyledNavItem>
  )
}
